<template>
    <div>
        <Header></Header>


        <section class="login_bg testr_ress">

            <div class="container">

                <div class="card_fathers ">
                    <div class="start_card">
                        <div class="start_card_header">
                            <div class="quiz_time">


                            </div>


                        </div>
                        <div class="single_tests" >
                            <div class="single_title">
                                {{test.title}}
                            </div>
                            <div class="single_test_f">
                                <div class="single_tests">
                                    <button :class="{ active: activeButton === inds }"
                                            @click="setActive(inds,ans)"
                                            class="test_choise" v-for="(ans,inds) in test.question_answers" :key="inds">
                                        {{ans.text}}
                                    </button>

                                </div>

                            </div>
                            <div class="prev_next_btn">
                                <div class="bottom_nextbtn prevbtns">
                                    <button @click="prevFunction(test,index)">

                                        <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                        <span v-if="!show_spinner"> {{$t('prev')}} </span>
                                    </button>
                                </div>
                                <div class="bottom_nextbtn">
                                    <button :class="{'active':next_active}"  @click="next_shows(test,index)">

                                        <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                        <span v-if="!show_spinner">{{$t('next')}}</span>

                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


        </section>
        <Footer></Footer>

    </div>
</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer"

    import html2pdf from "html2pdf.js";

    export default {
        components: {
            Header,
            Footer,

        },
        data() {
            return {
                next_active:true,
                result_user:'',
                result_date:'',
                rresulttest_category:'',
                start_test: true,
                is_active: 'NotGivn',
                test: [],
                index_main: 0,
                answeer: [],
                renge_items: 20,
                is_results: true,
                result_mains: [],
                show_spinner: false,
                time: 1990, // 33 minutes and 10 seconds in total seconds
                timer: null, // To store the interval reference
                get_ans:'',
                result_id:'',
                test_id:'',
                activeButton: null,
                result_mainss:[],
                resultquestions:''
            }
        },
        mounted() {
            this.startTimer()
            this.startFunction()
        },
        computed: {
            formattedTime() {
                const minutes = Math.floor(this.time / 60);
                const seconds = this.time % 60;
                return `${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
            },
        },

        methods: {
            setActive(index,ans) {
                this.next_active=false
                this.activeButton = index; // Set the clicked button as active
                console.log(ans)
                this.get_ans=ans
            },
            generatePDF() {
                const element = document.getElementById("pdf-content"); // Get the HTML content
                const options = {
                    margin: 1,
                    filename: "my-document.pdf",
                    image: { type: "jpeg", quality: 0.98 },
                    html2canvas: { scale: 3 },
                    jsPDF: { unit: "in", format: "letter", orientation: "portrait" },
                };

                // Use html2pdf to convert the content to PDF
                html2pdf().set(options).from(element).save();
            },
            startTimer() {
                if (this.timer) {
                    clearInterval(this.timer); // Clear any existing interval
                }
                this.timer = setInterval(() => {
                    if (this.time > 0) {
                        this.time--;
                    } else {
                        clearInterval(this.timer); // Stop the timer when it reaches 0
                        this.finished_tests()
                    }
                }, 1000);
            },
            next_shows(quiz) {
                console.log(quiz)
                this.test_id=quiz.id
                this.index_main++
                this.show_spinner=true
                this.$axios.post(`/test/questionnaire-start/`, {
                        "test_category_id":Number(this.$route.params.id),
                        "question_order":quiz.order,
                        "answer_id":this.get_ans.id,
                        "question_id":quiz.id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                             'Accept-Language':localStorage.getItem('lang')
                        }
                    },

                )
                    .then(res => {

                      console.log(res.data.test_result_id,"djsandjk")
                      this.result_id=res.data.test_result_id
                        this.next_active=true
                        this.start_test = false
                        this.test=res.data.questionnaire
                        this.show_spinner=false
                        console.log(this.test)
                        console.log(res.data,"res data")
                        if(res.data.questions){
                            this.$router.push('/my-answers')
                        }

                    })
                    .catch(()=>{
                        this.show_spinner=false

                    })
                this.activeButton=null
            },
            prevFunction() {
                this.show_spinner=true
                this.$axios.get(`test/questionnaire-active-test/?test_result_id=${this.result_id}&test_id=${this.test_id}`, {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                             'Accept-Language':localStorage.getItem('lang')
                        }
                    },

                )
                    .then(res => {
                      this.test=res.data.questions
                        this.start_test = false
                        this.tests = res.data
                        console.log(this.tests)
                        this.show_spinner=false
                    })
            },
            startFunction() {
                this.show_spinner=true
                this.$axios.post('/test/questionnaire-start/', {
                        "test_category_id":Number(this.$route.params.id),
                        "question_order":0,
                        "answer_id":0,
                        "question_id":0
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                             'Accept-Language':localStorage.getItem('lang')
                        }
                    }
                )
                    .then(res => {
                        this.start_test = false
                        this.test = res.data.questionnaire
                        this.show_spinner=false

                        this.result_id=this.tests.result_id
                    })

            },
            finished_tests() {
                this.show_spinner=true

                this.$axios.post('/test/test-finish/', {
                        "answers": this.answeer,
                        "result_id": this.tests.result_id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                             'Accept-Language':localStorage.getItem('lang')
                        }
                    }
                )
                    .then(res => {
                        this.is_results=true
                        this.result_mains = res.data
                        this.show_spinner=false
                    })
            }
        },
        beforeDestroy() {
            if (this.timer) {
                clearInterval(this.timer); // Clear the interval when the component is destroyed
            }
        },

    }
</script>