</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer";
    export default {
        components:{
            Header,
            Footer,
        },
        data(){
            return{
                page: 1,
                total: 2,
                perPage: 1,
                search:'',
                dataa:[],
                imageUrl: null,
                imagePosition: { x: 0, y: 0 },
                dragging: false,
                initialMousePos: { x: 0, y: 0 },
                imageWidth: 300,
                imageHeight: 400,
                savedImageUrl: null, // Store the data URL of the saved image
                change_images:false,
                show_spinner:false,
                show_spinner1:false,
                change_mail:'',
                headers: {
                    smail: '*_~'
                },
                is_up:false,
                imgDataUrl: '',
                change_password:'',
                change_confirm_pass:'',
                jshshr:'',
                class_name:'',
                school_name:'',
                school_id:'',
                district_name:'',
                district_id:'',
                gender_name:'',
                date_of_birth:'',
                full_name:'',
                region_id:'',
                region_name:'',
                gender_id:'',
                class_id:'',
                is_show_imgage:false,
                myImages:[],
                files_name:[],
                imageUrl1:[],
                myImages1:[],
                files_name1:[],
                interests:'',
                profile_cover:'',
                profile_image:'',
                documentnumber:'',
                documentseries:'',
                dateofbirth:'',
                user_me:'',
                last_name:'',
                first_name:'',
                firstNameInitial:'',
                lastNameInitial:'',
                errors:'',
                success:'',
                url: 'https://your-server.com/upload', // Replace with your actual server upload URL
                langType: {
                    hint: 'Rasm yuklash uchun bu joyni bosing yoki sudrab olib keling',
                    loading: 'Yuklanmoqda...',
                    noSupported: 'Sizning brauzeringiz rasm yuklashni qollab-quvvatlamaydi, iltimos brauzeringizni yangilang!',
                    success: 'Yuklash muvaffaqiyatli amalga oshirildi!',
                    fail: 'Yuklashda xato yuz berdi.',
                    preview: 'Oldindan korish',
                    btn: 'Rasmni tanlang',
                    btnTips: 'Rasmni tanlang yoki sudrab olib keling',
                    cut: 'Kesish',
                    uploading: 'Yuklanmoqda, biroz kuting...',
                    ready: 'Rasm yuklashga tayyor',
                    customTips: 'Faylni tanlang yoki sudrab olib keling',
                    auto: 'Avto',
                },
                uploadedImageUrl: '', // This will store the URL of the uploaded image

            }
        },
        watch: {
            page(page) {
                this.$axios.get(`/test/test-results/?page=${page}`,
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                            'Accept-Language':localStorage.getItem('lang')
                        }
                    }
                )
                    .then(res=>{
                        this.dataa=res.data
                    })

            },
        },
        mounted() {
            this.$axios.get(`/test/test-results/`,
                {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language':localStorage.getItem('lang')
                    }
                }
            )
                .then(res=>{
                    this.dataa=res.data
                })
          this.$axios.get('/v1/students/me/',{
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`,
              'Accept-Language':localStorage.getItem('lang')
            }
          })
              .then(res=>{
                this.user_me=res.data
                const parts = this.user_me.full_name.split(' ');

                // Familiya va ismni olish
                const lastName = parts[0]; // Familiya
                const firstName = parts[1]; // Ism
                this.first_name=firstName
                this.last_name=lastName
                const firstNameInitial = parts[0] ? parts[0][0] : ''; // Familiyaning bosh harfi
                const lastNameInitial = parts[1] ? parts[1][0] : '';  // Ismning bosh harfi
                this.firstNameInitial=firstNameInitial
                this.lastNameInitial=lastNameInitial


              })
        },
        computed: {
            imageStyle() {
                return {
                    transform: `translate(${this.imagePosition.x}px, ${this.imagePosition.y}px)`,
                    width: `${this.imageWidth}px`,
                    height: `${this.imageHeight}px`,
                };
            },
        },
        methods: {
            uploadImage(event) {
                const file = event.target.files[0];
                if (!file || !file.type.startsWith('image/')) {
                    alert("Please upload a valid image file.");
                    return;
                }

                const reader = new FileReader();
                reader.onload = (e) => {
                    this.imageUrl = e.target.result;
                };
                reader.readAsDataURL(file);
            },
            startDrag(event) {
                this.dragging = true;
                this.initialMousePos = { x: event.clientX, y: event.clientY };
                document.addEventListener("mousemove", this.onDrag);
                document.addEventListener("mouseup", this.stopDrag);
            },
            onDrag(event) {
                if (!this.dragging) return;

                const deltaX = event.clientX - this.initialMousePos.x;
                const deltaY = event.clientY - this.initialMousePos.y;
                this.imagePosition.x += deltaX;
                this.imagePosition.y += deltaY;
                this.initialMousePos = { x: event.clientX, y: event.clientY };
            },
            stopDrag() {
                this.dragging = false;
                document.removeEventListener("mousemove", this.onDrag);
                document.removeEventListener("mouseup", this.stopDrag);
            },
            resetImage() {
                this.imagePosition = { x: 0, y: 0 };
                this.imageWidth = 300;
                this.imageHeight = 400;
                this.savedImageUrl = null; // Clear saved image URL on reset
            },
            updateDimensions() {
                // This method is used for width, height, and position changes
            },
            saveImage() {
                if (!this.imageUrl) {
                    alert("No image to save.");
                    return;
                }

                const canvas = document.createElement("canvas");
                const ctx = canvas.getContext("2d");
                canvas.width = this.imageWidth;
                canvas.height = this.imageHeight;

                const image = new Image();
                image.src = this.imageUrl;
                image.onload = () => {
                    ctx.clearRect(0, 0, canvas.width, canvas.height); // Clear canvas before drawing
                    ctx.drawImage(image, this.imagePosition.x, this.imagePosition.y, this.imageWidth, this.imageHeight);
                    this.savedImageUrl = canvas.toDataURL("image/png"); // Convert to Data URL
                };
                this.is_show_imgage=false
            },
            handleImageChange(file) {
                console.log('Image changed:', file);
            },
            handleImageUpload(response) {
                console.log('Image uploaded:', response);
                if (response.status === 'success') {
                    this.uploadedImageUrl = response.data.url;
                    this.change_images=false// Assuming the server returns the uploaded image URL in response.data.url
                } else {
                    alert('File upload failed.');
                }
            },
            handleBack() {
                console.log('Back button clicked');
                // Logic to go back to the previous state or screen
                this.uploadedImageUrl = ''; // Example: Clear the uploaded image URL to reset the view
            },
            handleExit() {
                console.log('Exit button clicked');
                // Logic to handle the exit action, such as closing the modal or navigating away
                this.uploadedImageUrl = ''; // Example: Clear the uploaded image URL to reset the view
            },
            cropSuccess(imgDataUrl, field) {
                console.log(field, "hello");
                this.imgDataUrl = imgDataUrl;
            },
            /**
             * upload success
             *
             * [param] jsonData  server api return data, already json encode
             * [param] field
             */
            cropUploadSuccess(jsonData, field) {
                console.log('-------- upload success --------');
                console.log(jsonData);
                console.log('field: ' + field);
            },
            /**
             * upload fail
             *
             * [param] status    server api return error status, like 500
             * [param] field
             */
            cropUploadFail(status, field) {
                console.log('-------- upload fail --------');
                console.log(status);
                console.log('field: ' + field);
                console.log("dskajdnsak11")
            },

            filtCount() {
                if (this.jshshr > 999999999999) {
                    document.getElementById('jshshr').style.border = "1px solid red";
                }
            },
            changePasword() {
                const form = new FormData();
                form.append("password", this.change_password);
                form.append("confirm_password", this.change_confirm_pass);
                form.append("by_phone_number", this.change_mail);
                this.$axios.post('students/reset-password/', form)
                    .then(() => {
                        localStorage.setItem('token', this.$route.query.token)
                    })
                    .catch(error => {
                        console.log(error.message)

                        this.$toast(`${error.message}`, {
                            timeout: 4000,
                            type: "error"
                        });
                    })
            },
            trySubmitFile(e) {
                // console.log(e,"hdjahbdjhsa")
                // this.imageUrl=e
                // this.myImages.push(e)
                // this.is_up=false

                this.imageUrl = []
                for (let i = 0; i < e.target.files.length; i++) {
                    this.files_name.push(e.target.files[i])
                }
                var files = Array.from(e.target.files);
                console.log(typeof (files))
                console.log(files, "dnskajn")
                files.forEach((file) => {
                    this.myImages.push(file);
                    console.log(this.myImages[0], "sdnsakj")
                    var reader = new FileReader();
                    var vm = this;
                    reader.onload = function (e) {
                        vm.imageUrl.push(e.target.result);
                    };
                    reader.readAsDataURL(file);
                });
            },
            trySubmitFile1(e) {
                this.imageUrl1 = []
                for (let i = 0; i < e.target.files.length; i++) {
                    this.files_name1.push(e.target.files[i])
                }
                var files = Array.from(e.target.files);
                console.log(typeof (files))
                console.log(files, "dnskajn")
                files.forEach((file) => {
                    this.myImages1.push(file);
                    console.log(this.myImages1[0], "sdnsakj")
                    var reader = new FileReader();
                    var vm = this;
                    reader.onload = function (e) {
                        vm.imageUrl1.push(e.target.result);
                    };
                    reader.readAsDataURL(file);
                });
            },
            filtUser() {
                this.show_spinner=true
                this.$axios.get('/account/student-with-pinfl/', {
                    params: {
                        'pinfl': this.jshshr,
                        'token': localStorage.getItem('token')

                    },
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res => {
                        this.show_spinner=false
                        this.full_name = res.data.full_name
                        this.region_name = res.data.region_name
                        this.class_name = res.data.class_name,
                            this.school_name = res.data.school_name,
                            this.school_id = res.data.school_id,
                            this.district_name = res.data.district_name,
                            this.district_id = res.data.district_id
                        this.gender_name = res.data.gender_name,
                            this.gender = res.data.gender_id
                        this.region_id = res.data.region_id
                        this.class_id = res.data.class_id
                        this.date_of_birth = new Date(res.data.date_of_birth).toJSON().slice(0, 10).split('-').reverse().join('-')
                        this.success="JSHSHIR orqali shaxsingiz muvaffaqiyatli tasdiqlandi."
                    })
                    .catch(error => {
                        this.show_spinner=false
                        this.errors=error.response.data.error
                    })
            },
            filtUserP() {
                this.$axios.get('integration/teacher', {
                    params: {

                        'documentseries': this.documentseries,
                        'documentnumber': this.documentnumber,
                        'dateofbirth': this.dateofbirth,
                        'token': localStorage.getItem('token')
                    },
                })
                    .then(res => {
                        this.class_name = res.data.class_name,
                            this.school_name = res.data.school_name,
                            this.school_id = res.data.school_id,
                            this.district_name = res.data.district_name,
                            this.district_id = res.data.district_id
                        this.gender_name = res.data.gender_name,
                            this.gender = res.data.gender_id
                        this.date_of_birth = new Date(res.data.date_of_birth).toJSON().slice(0, 10).split('-').reverse().join('-'),
                            this.full_name = res.data.full_name
                        this.region_id = res.data.region_id
                        this.region_name = res.data.region_name
                        this.class_id = res.data.class_id
                    })
            },
            putUsers() {
                this.show_spinner1=true
                console.log(this.myImages1[0])

                const form = new FormData();
                // form.append("profile_image", this.myImages1[0]);
                // form.append("profile_cover", this.myImages[0]);
                form.append("full_name", this.full_name);
                form.append("date_of_birth", this.date_of_birth);
                form.append("gender", this.gender);
                form.append("interests", "Sport");
                form.append("region_id", this.region_id);
                form.append("region_name", this.region_name);
                form.append("district_id", this.district_id);
                form.append("district_name", this.district_name);
                form.append("school_id", this.school_id);
                form.append("school_name", this.school_name);
                form.append("class_id", this.class_id);
                form.append("class_name", this.class_name);
                this.$axios.patch(`account/students/profile/`, form,
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        }
                    })
                    .then(() => {
                        this.$toast(`Profil saqlandi`, {
                            timeout: 4000,
                            type: "success"
                        });
                        this.show_spinner1=false
                        this.$axios.get(`students/me/`,
                            {
                                headers: {
                                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                                    'Accept-Language': 'oz'
                                }
                            }
                        )
                            .then(res => {
                                this.$axios.get(`students/${res.data.id}/profile-settings`, {
                                    headers: {
                                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                                    }
                                })
                                    .then(res => {
                                        this.$toast("Ma'lumot yangilandi", {
                                            timeout: 10000,
                                            type: "success"
                                        });
                                        this.class_name = res.data.class_name,
                                            this.school_name = res.data.school_name,
                                            this.school_id = res.data.school_id,
                                            this.district_name = res.data.district_name,
                                            this.district_id = res.data.district_id
                                        this.gender_name = res.data.gender_name,
                                            this.gender = res.data.gender
                                        this.date_of_birth = new Date(res.data.date_of_birth).toJSON().slice(0, 10).split('-').reverse().join('-'),
                                            this.full_name = res.data.full_name
                                        this.region_id = res.data.region_id
                                        this.region_name = res.data.region_name
                                        this.class_id = res.data.class_id
                                        this.profile_image = res.data.profile_image_url
                                        this.profile_cover = res.data.profile_cover_url
                                        this.interests = res.data.interests
                                    })

                            })

                    })
                    .catch(error => {
                        this.show_spinner1=false
                        console.log(error.response.data.error)
                        this.$toast(`${error.response.data.error}`, {
                            timeout: 4000,
                            type: "error"
                        });

                    })
            }
        }
    }
</script>