<template>
    <div class="test_bg">
    <Header></Header>
        <div class="container">
                    <div class="main_docs_title">
                        Meyoriy-huquqiy hujjatlar
                    </div>
                <div class="main_docs">
                    <button @click="get_docs(3)" :class="{'active':isActive==3}">
                        Prezident farmon va qarorlari
                    </button>
                    <button @click="get_docs(4)" :class="{'active':isActive==4}">
                        Vazirlik mahkamasi qarorlar
                    </button>
                    <button @click="get_docs(5)" :class="{'active':isActive==5}">
                        Vazirlikning buyruqlari
                    </button>
                    <button  @click="get_docs(6)" :class="{'active':isActive==6}">
                        Vazirlikning xatlari
                    </button>
                    <button  @click="get_docs(7)" :class="{'active':isActive==7}">
                        Kalendar rejalar
                    </button>
                    <button @click="get_docs(8)" :class="{'active':isActive==8}">
                        Oquv dasturlar
                    </button>
                </div>
            <div class="row" v-if="docs">
                <div class="col-xl-6" v-for="(item,index) in docs.results" :key="index">
                    <a :href="item.file" class="text-decoration-none">
                        <div class="docs_card">
                            <div>
                                <img src="../assets/images/pdf.png" alt="">
                            </div>
                            <div>
                                <div>
                                    {{item.name}}
                                </div>
                                <div class="date_files">
                                    {{item.date_field}}
                                </div>
                            </div>
                        </div>
                    </a>

                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    export default {
        components:{
            Header,
            Footer
        },
        data() {
        return {
            isActive: 3,
            is_docs:[],
            file_category:3,

        }
    },
    mounted() {
        this.$axios.get(`v1/docs_main?filescategory=${this.isActive}`,{
            headers:{
                'Accept-Language':localStorage.getItem('lang')
            }
        })
            .then(res=>{
                this.docs=res.data
            })
    },
    methods:{
        get_docs(index)
        {
            this.$axios.get(`v1/docs_main?filescategory=${index}`,{
                headers:{
                    'Accept-Language':localStorage.getItem('lang')
                }
            })
            .then(res=>{
                this.docs=res.data
                this.isActive=index
            })

        }
    },


    }
</script>