<template>
    <div>
        <Header></Header>
        <div class="test_bg">
            <div class="container">
                <div class="row">
                    <div class="col-xl-2">
                        <img src="../assets/cloud1.png" alt="">
                    </div>
                    <div class="col-xl-8">
                        <div class="calc_texts">
                            <div class="cal_title">
                               {{$t('title')}}
                            </div>
                            <div class="cal_desc">
                              {{$t('description')}}
                            </div>
                        </div>
                        <div class="subjects">

                            <label  :class="{'active':dynamicItems[index].active}" @click="getWork(item)" v-for="(item,index) in main_categorys" :key="index">
                                <input class="d-none" @input="getWork(item,index)" type="checkbox" v-model="dynamicItems[index].active">
                                {{item.title}}
                                <div class="show_icons" :class="{'active':dynamicItems[index].active}">

                                    <svg width="16.000000" height="16.000000" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                                        <desc>
                                            Created with Pixso.
                                        </desc>
                                        <defs>
                                            <clipPath id="clip12_14959">
                                                <rect id="mb / 16 / cancel" rx="-0.500000" width="15.000000" height="15.000000" transform="translate(0.500000 0.500000)" fill="white" fill-opacity="0"/>
                                            </clipPath>
                                        </defs>
                                        <rect id="mb / 16 / cancel" rx="-0.500000" width="15.000000" height="15.000000" transform="translate(0.500000 0.500000)" fill="#FFFFFF" fill-opacity="0"/>
                                        <g clip-path="url(#clip12_14959)">
                                            <path id="Vector" d="M12 4L4 12" stroke="#FFFFFF" stroke-opacity="1.000000" stroke-width="1.600000" stroke-linejoin="round" stroke-linecap="round"/>
                                            <path id="Vector" d="M4 4L12 12" stroke="#FFFFFF" stroke-opacity="1.000000" stroke-width="1.600000" stroke-linejoin="round" stroke-linecap="round"/>
                                        </g>
                                    </svg>

                                </div>
                            </label>
                        </div>
                    </div>
                    <div class="col-xl-2">
                        <img src="../assets/cloud2.png" alt="">
                    </div>
                </div>
                <div class="row">
                    <div class="col-xl-6 card_mains_job" v-for="(item,indexs) in jobs.results" :key="indexs">

                        <div class="jobs_cards">
                            <div>
                                <img :src="item.cover_image_url" alt="">
                            </div>
                            <div>
                                <div class="job-title">
                                    {{item.title}}
                                </div>
                                <div class="job_des" v-html="item.workplace">

                                </div>
                                <div class="job_price">
                                    <span>  Oylik maoshi:</span>
                                    {{item.salary_range}} som
                                </div>
                                <div class="jobs_link">
                                    <router-link :to="`/jobs/${item.id}`">
                                        Batafsil tanishish
                                        <div>
                                            <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
                                                 xmlns="http://www.w3.org/2000/svg">
                                                <path d="M10 7L15 12L10 17" stroke="#8F51E6" stroke-width="2"
                                                      stroke-linecap="round" stroke-linejoin="round"/>
                                            </svg>

                                        </div>
                                    </router-link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer";
    export default {
        components:{
            Header,
            Footer
        },
        data(){
            return{
                cres:[],
                subId:[],
                isSubject:false,
                isActive:[],
                Sub:[],
                dynamicItems: [
                ],
                selectedAll:false,
                jobs:[],
                page: 1,
                total: 2,
                perPage: 1,
                search:'',
                jobs_category:[],
                selects_item:'',
                selects_price:'',
                main_categorys:[]
            }
        },
        mounted() {
            for (var i=0;i<30;i++){
                this.dynamicItems.push({
                    active:false
                })
            }
            this.$axios.get('/v1/professions-web/?page=1',{
                headers:{
                    'Accept-Language':localStorage.getItem('lang')
                }
            })
                .then(res => {
                    this.jobs = res.data
                })
            this.$axios.get(`v1/subjects/`,{
                headers:{
                    'Accept-Language':localStorage.getItem('lang')
                }
            })
                .then(res=>{
                    this.main_categorys=res.data
                })
        },
        methods: {

            getWork(item) {
                var k=''
                var index = this.Sub.indexOf(item);

                if (index === -1) {
                    this.Sub.push(item);
                } else {
                    this.Sub.splice(index, 1);
                }

                this.Sub.forEach(el=>{
                    k=k+`subjects=${el.id}&`
                })
                this.$axios.get(`v1/professions-web-calculator?${k}`,{
                    headers:{
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res=>{
                       this.jobs=res.data
                    })
            },
        }
        }
</script>