                </div>
                <div class="log_title">
                    Foydalanuvchi parolini tiklash
                </div>

                <div class="reg_tab">
                    <button :class="{'active':is_active==='phone_number'}" @click="is_active='phone_number'">
                        Telefon raqam
                    </button>
                    <button :class="{'active':is_active==='email'}" @click="is_active='email'">
                        Pochta
                    </button>
                </div>
                <div class="tab_1" v-if="is_active==='phone_number'">
                    <form  @submit.prevent="registerUser">
                        <div class="phone_input">
                            <label>Telefon raqamingizni kiriting</label>
                            <div>
                                <input type="text" v-model="phone_number" placeholder="+998 90 000 00 00">
                            </div>
                        </div>
                        <div class="router_reg">
                            <button>
                                <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                <span v-if="!show_spinner">{{$t('continue')}}</span>
                            </button>
                        </div>
                    </form>
                </div>
                <div class="tab_1" v-if="is_active==='email'">
                    <form  @submit.prevent="registerUser1">
                        <div class="phone_input">
                            <label>{{$t('enter_postal_address')}}</label>
                            <div>
                                <input v-model="email" type="text" placeholder="user@gmail.com">
                            </div>
                        </div>
                        <div class="router_reg">
                            <button>
                                <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                <span v-if="!show_spinner">{{$t('continue')}}</span>
                            </button>
                        </div>
                    </form>
                </div>
            </div>


        </section>
        <Footer></Footer>

    </div>
</template>
<script>
    import Header from '../../components/Header.vue'
    import Footer from '../../components/Footer.vue'
    export default {
        components:{
            Header,
            Footer
        },
        data(){
            return {
              isPasswordVisible: false,
                is_active:'phone_number',
                item1:'',
                item2:'',
                item3:'',
                item4:'',
                is_code:false,
                phone_number:'',
                email:'',
                username:'',
                show_code:false,
                show_spinner:false,
                username_main:'',
                password:'',
                user_id:'',
                is_username:false,
                guid:'',
                by_phone_number:true
            }
        },
        mounted() {
            console.log(this.$route.query.token)
            if(this.$route.query.token!=undefined){
                this.by_phone_number=false
                this.show_spinner=false
                this.is_username=true
                let formData = new FormData();
                formData.append("auth_type", "email");
                formData.append("token", this.$route.query.token);
                this.show_spinner=true
                this.$axios.post('/v1/students/verify-reset-password/',formData)
                    .then(res=>{
                        console.log(res.data)
                        this.guid=res.data.guid
                        this.user_id=res.data.user_id
                        this.show_spinner=false
                        this.is_username=true
                    })
                    .catch(error=>{
                        this.show_spinner=false
                        this.$toast(`${error}`, {
                            timeout: 2000,
                            type:"error"
                        });
                    })
            }
        },
      computed: {
        passwordFieldType() {
          return this.isPasswordVisible ? 'text' : 'password';
        },
        passwordIcon() {
          return this.isPasswordVisible ? 'eye-slash' : 'eye';
        }
      },
      methods:{
        togglePasswordVisibility() {
          this.isPasswordVisible = !this.isPasswordVisible;
        },
            moveFocus(event, nextFieldId) {
                if (event.target.value.length === 1 && nextFieldId) {
                    document.getElementById(nextFieldId).focus();
                }
            },
            myFunction(){
                if(this.item1<0||this.item1>9){
                    this.item1=''
                }
            },
            myFunction1(){
                if(this.item2<0||this.item2>9){
                    this.item2=''
                }
            },
            myFunction2(){
                if(this.item3<0||this.item3>9){
                    this.item3=''
                }
            },
            myFunction3(){
                if(this.item4<0||this.item4>9){
                    this.item4=''
                }
            },
            registerUser() {
                let formData = new FormData();
                formData.append("phone_number", this.phone_number);
                this.show_spinner=true
                this.$axios.post('/v1/students/forgot-password/', formData)
                    .then(()=>{
                        this.show_spinner=false
                        this.show_code=true
                    })

                    .catch(error=>{
                        this.show_code=false
                        this.show_spinner=false
                        this.$toast(`${error}`, {
                            timeout: 2000,
                            type:"error"
                        });
                    })
            },
            registerUser1() {
                this.show_spinner=true
                let formData = new FormData();
                formData.append("email", this.email);
                this.show_spinner=true
                this.$axios.post('/v1/students/forgot-password/', formData)

                    .then(()=>{
                        this.show_spinner=false
                        this.show_code=true
                    })
                    .catch(()=>{
                        this.show_code=false
                        this.show_spinner=false
                    })
            },
            isCodeActive1() {
                let formData = new FormData();
                formData.append("auth_type", this.is_active);
                formData.append("phone_number", this.phone_number);
                formData.append("code", `${this.item1}`+this.item2+this.item3+this.item4);
                this.show_spinner=true
                this.$axios.post('/v1/students/verify-reset-password/',formData)
                    .then(res=>{
                        console.log(res.data)
                        this.guid=res.data.guid
                        this.user_id=res.data.user_id
                        this.show_spinner=false
                        this.is_username=true
                    })
                    .catch(error=>{
                        this.show_spinner=false
                        this.$toast(`${error}`, {
                            timeout: 2000,
                            type:"error"
                        });
                    })

            },
            isCodeActive2() {
                this.show_spinner=true
                this.$axios.post('/account/verify/', {
                    "email":this.email,
                    "code":`${this.item1}`+this.item2+this.item3+this.item4,
                    "user_type":this.$route.params.user_type,
                    "auth_type":this.is_active
                })
                    .then(res=>{
                        this.show_spinner=false
                        console.log(res.data)
                        this.guid=res.data.guid
                        this.user_id=res.data.user_id
                        this.show_spinner=false
                        this.is_username=true

                    })
                    .catch(error=>{
                        this.show_spinner=false
                        this.$toast(`${error}`, {
                            timeout: 2000,
                            type:"error"
                        });
                    })

            },
            SetInfo(){
                let formData = new FormData();
                if(this.$route.query.token!=undefined){

                    formData.append("confirm_password", this.username_main);
                    formData.append("password", this.password);

                    formData.append("token", this.$route.query.token);
                  formData.append("guid", this.guid);
                }
                else{
                    formData.append("confirm_password", this.username_main);
                    formData.append("password", this.password);
                    formData.append("by_phone_number", true);
                    formData.append("guid", this.guid);
                }
                this.$axios.post('/v1/students/reset-password/', formData)
                    .then(res=>{
                        localStorage.setItem("token",res.data.password)
                        this.$router.push('/login')
                    })
                    .catch(error=>{
                        this.show_code=false
                        this.show_spinner=false
                        this.$toast(`${error}`, {
                            timeout: 2000,
                            type:"error"
                        });
                    })
            }
        }

    }
</script>