                        </div>

                    </div>
                </div>
                <div class="card_fathers">
                    <div class="start_card" v-if="start_test==false&&show_result==false">
                        <div>
                            <div class="start_card_tit">
                                {{testct.results[$route.query.index].title}}
                            </div>
                            <div class="start_card_des">
                                {{testct.results[$route.query.index].description}}
                            </div>
                        </div>
                        <div class="quiz_firsts">
                            <div class="num_f">
                                <div class="num_one">
                                    1
                                </div>
                                <div class="num_title">
                                    Testni boshlash uchun Testni boshlash tugmasini bosing;
                                </div>
                            </div>
                            <div class="num_f">
                                <div class="num_one">
                                  2
                                </div>
                                <div class="num_title">
                                    Berilgan savoldan kelib chiqib HA yoki YOQ javobni belginlang.
                                </div>
                            </div>
                        </div>
                        <div class="notes_main">
                          {{testct.results[$route.query.index].instruction}}
                        </div>
                        <div class="start_buttons">
                            <button class="starts_cencel" @click="$router.push('/test_category')">
                                Bekor qilish
                            </button>
                            <button class="starts_start" @click="startFunction()">
                                <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                <span v-if="!show_spinner">Testni boshlash</span>

                            </button>
                        </div>
                    </div>
                </div>
                <div class="card_fathers " v-if="(is_results==true||start_test==true)&&show_result==false">
                    <div class="start_card">
                        <div class="start_card_header">
                            <div class="quiz_time">

                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
                                     xmlns="http://www.w3.org/2000/svg">
                                    <path d="M17.6177 5.9681L18.7175 4.86827C18.9128 4.67301 19.2294 4.67301 19.4247 4.86827L20.1317 5.57537C20.327 5.77064 20.327 6.08722 20.1317 6.28248L19.0319 7.38231C20.2635 8.92199 21 10.875 21 13C21 17.9706 16.9706 22 12 22C7.02944 22 3 17.9706 3 13C3 8.02944 7.02944 4 12 4C14.125 4 16.078 4.73647 17.6177 5.9681ZM11.5 8C11.2239 8 11 8.22386 11 8.5V13.5C11 13.7761 11.2239 14 11.5 14H12.5C12.7761 14 13 13.7761 13 13.5V8.5C13 8.22386 12.7761 8 12.5 8H11.5ZM8 1.5C8 1.22386 8.22386 1 8.5 1H15.5C15.7761 1 16 1.22386 16 1.5V2.5C16 2.77614 15.7761 3 15.5 3H8.5C8.22386 3 8 2.77614 8 2.5V1.5Z"
                                          fill="#FA8F42"/>
                                </svg>

                                {{ formattedTime }}
                            </div>
                            <div class="quiz_count" v-if="tests.questions">
                                {{index_main+1}} / {{tests.questions.length}}
                            </div>
                            <div>
                                <button class="fineshed_btn "  @click="finished_tests()">
                                    <b-spinner v-if="show_spinner" variant="white" ></b-spinner>
                                    <span v-if="!show_spinner">   Testni tugatish</span>

                                </button>
                            </div>
                        </div>
                        <div v-show="index_main==index" v-for="(quiz,index) in tests.questions" :key="index">
                            <div>
                                <div class="quiz">
                                    {{quiz.title}}
                                </div>
                                <div class="quiz_btn">
                                    <button class="yes_btn" :class="{'active':is_active==1}" @click="isBoolen(ans_index=1,index);next_shows(quiz,index)" >
                                        Ha
                                    </button>
                                    <button class="yes_btn" :class="{'active':is_active==0}" @click="isBoolen(ans_index=0,index);next_shows(quiz,index)">
                                        Yo'q
                                    </button>
                                </div>
                            </div>
                            <!-- <div class="bottom_nextbtn">

                                <button :class="{'active':next_active}" @click="next_shows(quiz,index)">
                                    Davom etish
                                </button>
                            </div> -->
                        </div>
                    </div>
                </div>
            </div>


        </section>
        <Footer></Footer>

    </div>
</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer"
    // you probably need to import built-in style
    import 'vue-range-slider/dist/vue-range-slider.css'
    import html2pdf from "html2pdf.js";

    export default {
        components: {
            Header,
            Footer,
        },
        data() {
            return {
                start_test: false,
                is_active: 'NotGivn',
                tests: [],
                index_main: 0,
                next_active:true,
                next_finished:true,
                answeer: [],
                renge_items:20,
                is_results:false,
                result_mains:[],
                show_spinner:false,
                time: 1990, // 33 minutes and 10 seconds in total seconds
                timer: null, // To store the interval reference
                testct:[],
              show_result:false
            }
        },
        mounted() {
          this.startTimer()
            this.$axios.get('/test/test-category/',{
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':localStorage.getItem('lang')
                }
            })
                .then(res=>{
                    this.testct=res.data
                })
        },
        computed: {
            formattedTime() {
                const minutes = Math.floor(this.time / 60);
                const seconds = this.time % 60;
                return `${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
            },
        },

        methods: {
            isBoolen(ans_index,index){
                console.log(index)
                console.log(this.tests.questions.length,"uzunligi")
                if(ans_index==0){
                    this.is_active=0
                    this.next_active=false
                    this.next_finished=false
                }
                else if(ans_index==1){
                    this.is_active=1
                    this.next_active=false
                    this.next_finished=false
                }

               if((this.tests.questions.length-1)===index){
                    this.next_active=true
               }
                if((this.tests.questions.length-2)===index){
                    this.next_finished=true
                }

            },
            generatePDF() {
                const element = document.getElementById("pdf-content"); // Get the HTML content
                const options = {
                    margin: 1,
                    filename: "my-document.pdf",
                    image: { type: "jpeg", quality: 0.98 },
                    html2canvas: { scale: 3 },
                    jsPDF: { unit: "in", format: "letter", orientation: "portrait" },
                };

                // Use html2pdf to convert the content to PDF
                html2pdf().set(options).from(element).save();
            },
            startTimer() {
                if (this.timer) {
                    clearInterval(this.timer); // Clear any existing interval
                }
                this.timer = setInterval(() => {
                    if (this.time > 0) {
                        this.time--;
                    } else {
                        clearInterval(this.timer); // Stop the timer when it reaches 0
                        this.finished_tests()
                    }
                }, 1000);
            },
            next_shows(quiz,index) {
                this.next_active=true
                this.next_finished=true
                if((index<=this.tests.questions.length-2)){
                    this.next_active=true
                this.index_main++
                }
                this.answeer.push({
                    "question_id": quiz.id,
                    "answer": this.is_active
                })
                console.log(this.answeer)
                this.is_active="test"
            },
            startFunction() {
                this.show_spinner=true
                this.$axios.post('/test/test-start/', {
                        "test_category_id": this.$route.params.id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                          'Accept-Language':localStorage.getItem('lang')
                        }
                    }
                )
                    .then(res => {
                        this.start_test = true
                        this.is_results=false
                        this.tests = res.data
                        this.show_spinner=false
                    })
            },
            finished_tests() {
                this.show_spinner=true
                this.start_test=false

                this.$axios.post('/test/test-finish/', {
                        "answers": this.answeer,
                        "result_id": this.tests.result_id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                          'Accept-Language':localStorage.getItem('lang')
                        }
                    }
                )
                    .then(res => {
                        this.is_results=true
                        this.result_mains = res.data
                        this.show_spinner=false
                      this.show_result=true
                    })
            }
        },
        beforeDestroy() {
            if (this.timer) {
                clearInterval(this.timer); // Clear the interval when the component is destroyed
            }
        },

    }
</script>