                      <path fill-rule="evenodd" clip-rule="evenodd" d="M22.4859 5H17.5193C16.1959 5 15.7243 5.075 15.2109 5.24C14.6976 5.405 14.2526 5.66167 13.8526 6.02167C13.4509 6.38 13.1476 6.75167 12.4809 7.89333L11.2526 10H8.6776C6.81927 10 6.14594 10.1933 5.4676 10.5567C4.78927 10.92 4.25594 11.4533 3.8926 12.1317C3.52927 12.81 3.33594 13.4833 3.33594 15.3417V27.9917C3.33594 29.85 3.52927 30.5233 3.8926 31.2017C4.24783 31.8727 4.79658 32.4214 5.4676 32.7767C6.14594 33.14 6.81927 33.3333 8.6776 33.3333H31.3276C33.1859 33.3333 33.8593 33.14 34.5376 32.7767C35.2086 32.4214 35.7574 31.8727 36.1126 31.2017C36.4759 30.5233 36.6693 29.8483 36.6693 27.9917V15.3417C36.6693 13.4833 36.4759 12.81 36.1126 12.1317C35.7493 11.4533 35.2159 10.92 34.5376 10.5567C33.8593 10.1933 33.1843 10 31.3276 10H28.7526L27.5243 7.89333C26.8576 6.75167 26.5526 6.38 26.1526 6.02167C25.7509 5.66167 25.3076 5.40667 24.7943 5.24C24.2809 5.075 23.8093 5 22.4859 5ZM20.002 28C23.8679 28 27.002 24.866 27.002 21C27.002 17.134 23.8679 14 20.002 14C16.136 14 13.002 17.134 13.002 21C13.002 24.866 16.136 28 20.002 28Z" fill="#B1B1B3"/>
